$("._open-order").click(function () {
    let parent = $(this).parents(".gallery-slide")
    let title = $(parent).find(".gallery__title").html().trim()
    let imgSrc = $(parent).find(".gallery__img img").attr("src")

    $(".order-modal__title").html(title)
    $(".order-modal__img img").attr("src",imgSrc)
    $(".order-modal").addClass("_active")
    $("body").addClass("fixed-body")
})

if(window.innerWidth<577){
    $("._show-detail").click(function () {
        let parent = $(this).parents(".gallery-slide")
        let title = $(parent).find(".gallery__title").html().trim()
        let desc = $(parent).find(".gallery__desc").html().trim()
        let info = $(parent).find(".gallery__info").html()
        let imgSrc = $(parent).find(".gallery__img img").attr("src")

        $(".gallery-modal__title").html(title)
        $(".gallery-modal__desc").html(desc)
        $(".gallery__info").html(info)
        $(".gallery-modal__img img").attr("src",imgSrc)
        $(".gallery-modal").addClass("_active")
        $("body").addClass("fixed-body")
    })
}



$(".gallery-modal__back").click(function () {
    $(".gallery-modal").removeClass("_active")
    $("body").removeClass("fixed-body")
})

$("._close-order").click(function () {
    $(".order-modal").removeClass("_active")
    $("body").removeClass("fixed-body")
})

$(".investment__more").click(function () {
    $(this).toggleClass("_active")
    $(".investment ._hidden-text").slideToggle()
})

$(".gallery__more").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".gallery__desc").toggleClass("_expanded")
})

setTimeout(()=>{
    $("[data-src]").each(function (i,el) {
        let src = $(el).attr("data-src")
        $(el).attr("src",src)
    })
},1000)

$("._anchor-link").click(function(event) {
    event.preventDefault();

    var idc = $(this).attr('href'),
        top = $(idc).offset().top;
    $('body,html').animate({ scrollTop: top }, 500);

});


fix100vh();
function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}
