let degree = 0
let current = 0
let pageSlider = new Swiper(".gallery",{
    wrapperClass: "gallery__wrapper",
    slideClass: "gallery-slide",
    allowTouchMove: false,
    direction: "vertical",
    // hashNavigation: true,
    hashNavigation: {
        watchState: true,
    },
    slidesPerView: "auto",
    speed: 1000,

    parallax: true,

    watchOverflow: true,

    observer: true,
    observeParents: true,
    observeSlideChildren: true,

    keyboard: {
        enabled: true,
        onlyInViewport: true,
        pageUpDown: true
    },

    mousewheel: {
        sensitivity: 1,
    },

    init: false,

    on: {
        init: function () {
            renderGalleryNav(this.activeIndex)
            renderGalleryButtons(this.activeIndex)


        },
        slideChange: function () {
            renderGalleryNav(this.activeIndex)
            renderGalleryButtons(this.activeIndex)
        },
        slideChangeTransitionStart: function() {
            rotateBox()
        },
        resize: function () {

        }
    },
    breakpoints: {
        992: {

        }
    }
})

$("._next-slide").click(function () {
    let slidesCount = $(".gallery-slide").length
    if(pageSlider.activeIndex<slidesCount-1){
        pageSlider.slideTo(pageSlider.activeIndex+1,1000,false)
    }else{
        pageSlider.slideTo(0,1000,false)
    }
    rotateBox()
    galleryFullScreen()
})

$("._prev-slide").click(function () {
    let slidesCount = $(".gallery-slide").length
    if(pageSlider.activeIndex === 0){
        pageSlider.slideTo(slidesCount-1,1000,false)
    }else{
        pageSlider.slideTo(pageSlider.activeIndex-1,1000,false)
    }
    rotateBox()
    galleryFullScreen()
})

function renderGalleryButtons(activeIndex){
    let prevBtnText = ''
    let nextBtnText = ''
    let slidesCount = $(".gallery-slide").length

    prevBtnText = $(`.gallery-slide:last-child`).find(".gallery__title").html().trim()
    nextBtnText = $(`.gallery-slide:nth-child(1)`).find(".gallery__title").html().trim()

    if(activeIndex !== 0){
        prevBtnText = $(`.gallery-slide:nth-child(${activeIndex})`).find(".gallery__title").html().trim()
    }

    if(activeIndex<slidesCount-1){
        nextBtnText = $(`.gallery-slide:nth-child(${activeIndex+2})`).find(".gallery__title").html().trim()
    }

    $(".gallery__prev").addClass("_disable")
    $(".gallery__next").addClass("_disable")


    $(".gallery__prev span").html(prevBtnText)
    $(".gallery__next span").html(nextBtnText)

    setTimeout(()=>{
        $(".gallery__prev").removeClass("_disable")
        $(".gallery__next").removeClass("_disable")
    },900)
}

function renderGalleryNav(activeIndex){
    let links = []
    let activeClass = ''
    $(".gallery-nav__links").addClass("_disable")

    setTimeout(()=>{
        $(".gallery-nav__links .gallery-nav__link").remove()
        $(".gallery-menu__links .gallery-nav__link").remove()
        $(".gallery-nav__links").removeClass("_disable")
        $(".gallery__title").each(function (index,el) {
            let link = {
                title: $(el).html().trim(),
                index,
                class: index === activeIndex ? "_active" : ""
            }
            links.push(link)
        })

        for(let i=0;i<7;i++){
            let link = links.pop()
            links.unshift(link)
        }

        if(activeIndex>0){

            for(let i=0;i<activeIndex;i++){
                let link = links.shift()
                links.push(link)
            }
        }

        // console.log(links)



        links.forEach(link => {
            $(".gallery-menu__links").append(`
                <div data-index="${link.index}" class="gallery-nav__link ${link.class}">
                    <span>${link.title}</span>
                </div>
            `)
        })

        // if (activeIndex > 7){
        //     for(let i=0;i<activeIndex-7;i++){
        //         if(links.length > 17){
        //             let link = links.shift()
        //             console.log(link)
        //
        //         }
        //     }
        // } else{
        //     // links[links.length-1] = link
        //     let link = links.shift()
        //     links.unshift(link)
        // }


        links.forEach(link => {
            $(".gallery-nav__links").append(`
                <div data-index="${link.index}" class="gallery-nav__link ${link.class}">
                    <span>${link.title}</span>
                </div>
            `)
        })


    },300)
}

$(document).on('click', '.gallery-nav__link', function(e) {
    let slideIndex = +$(this).attr("data-index")
    pageSlider.slideTo(slideIndex,1000,false)
    rotateBox()
    $(".header").removeClass("_gallery-open")
    $("body").removeClass("fixed-body")
    galleryFullScreen()
})


const resizeHandlerSlider = () => {
    pageSlider.init()
}

resizeHandlerSlider()

window.addEventListener('resize', resizeHandlerSlider);


if (window.innerWidth>993){
    $('.gallery-slide:first-child').bind('wheel', function(e){
        if (e.originalEvent.wheelDelta >= 0){
            galleryScrollPrev()
        }
    })

    $('.gallery-slide:last-child').bind('wheel', function(e){
        if (e.originalEvent.wheelDelta <= 0){
            galleryScrollNext()
        }
    })

    $(".gallery").click(function () {
        galleryFullScreen()
    })

    $('.gallery').mouseenter(function () {
        galleryFullScreen()
    })
}

function galleryScrollPrev(){
    let top = $(".gallery").offset().top
    let screenHeight = $(window).height();
    $('body,html').animate({
        scrollTop: top - screenHeight
    }, 100);
}

function galleryScrollNext(){
    let top = $(".gallery").offset().top
    let screenHeight = $(window).height();
    $('body,html').animate({
        scrollTop: top + screenHeight
    }, 0);
}


let gallery = document.querySelector('.gallery')


function galleryFullScreen() {
    let footer = document.querySelector('.footer').getBoundingClientRect()

    let wrapperHeight = $(".wrapper").height();
    let footerHeight = footer.height;
    let screenHeight = $(window).height();

    $('body,html').animate({
        scrollTop: wrapperHeight - footerHeight - screenHeight
    }, 100);
}

$('.gallery-slide').each(function (i,el) {
    $(el).attr('data-degree',degree)
    degree = degree + 6
})

function rotateBox() {
    let degree = +$('.gallery-slide.swiper-slide-active').attr('data-degree')
    $(".gallery__box").css("transform",`rotate(-${degree}deg)`)
}

