$(window).bind('hashchange',function() {
    hashCheck()
});

$(document).ready(function() {
    hashCheck()
});

function hashCheck() {
    let hash = location.hash;


    $(".gallery-slide").each(function (i,el) {
        let dataHash = $(el).attr("data-hash")
        if(hash == `#${dataHash}`){
            galleryFullScreen()
        }
    })
}
